import React from 'react';
import classes from './Input.css';

const input = (props)=> {

    let inputElement = [];
    const inputClasess = [classes.InputElements];
    if (props.invalid && props.shouldValidate && props.inputTouched) {
        inputClasess.push(classes.Invalid);
    };

    switch (props.elementtype) {
        case ('input'): 
        inputElement = <input 
            className={inputClasess.join(' ')} 
            {...props.elementconfig}
            value={props.value}
            onChange={props.changed} />
            
        break;
        case('textarea'):
        inputElement = <textarea 
             className={inputClasess.join(' ')}
             {...props.elementconfig}
             value={props.value}
             onChange={props.changed} />
             
        break;
        case('select'):
        inputElement = 
        <select className={inputClasess.join(' ')}
            value={props.value}
            onChange={props.changed} >
                {props.elementconfig.options.map(option =>(
                <option key={option.value}
                 value={option.value}>
                 {option.displayValue}
                 </option>
            ))}

        </select>
        break;
        default: 
        inputElement = <input 
            className={inputClasess.join(' ')} 
            {...props.elementconfig}
            value={props.value}
            onChange={props.changed} />
    }

    return(
        <div className={classes.Input}>
            <label className={classes.Label}>{props.label}</label>
            {inputElement}
        </div>
    )
};

export default input;


